<template>
  <div class="goods_containner">
    <!-- 面包屑导航 -->
    <bread :first="'商品管理'" :second="'商品列表'"></bread>
    <!-- 卡片 -->
    <el-card class="box-card">
      <!-- 搜索框 -->
      <div class="search">
        <el-input placeholder="请输入内容" v-model="params.query" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="getGoods"></el-button>
        </el-input>
        <!-- 按钮 -->
        <el-button type="primary" class="addBtn" @click="addGoods">添加商品</el-button>
      </div>
      <!-- 表格 -->
      <template class="table">
        <el-table :data="tableData" style="width: 100%" border>
          <!-- 序号 -->
          <el-table-column label="#" width="60">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.$index + 1 }}</span>
            </template>
          </el-table-column>
          <el-table-column label="商品名称">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.goods_name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="商品价格">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.goods_price }}</span>
            </template>
          </el-table-column>
          <el-table-column label="商品重量">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.goods_weight }}</span>
            </template>
          </el-table-column>
          <el-table-column label="创建时间">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.add_time | format }}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" :aa="scope.row" size="mini"></el-button>
              <el-button type="danger" size="mini" icon="el-icon-delete"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!-- 分页功能 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[50, 100, 300, 500]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tableData: [],
      total: 0,
      params: {
        query: '', // 查询参数 非必要参数
        pagenum: 1, // 当前页码数
        pagesize: 50 // 每页显示条数
      },
      currentPage4: 1
    }
  },
  created () {
    this.getGoods()
  },
  methods: {
    // 获取数据的函数
    async getGoods () {
      const res = await this.axios.get('/goods', { params: this.params })
      // 将获取到的数据存到本地参数
      this.total = res.data.data.total
      // console.log(res.data.data.total,88888);
      this.tableData = res.data.data.goods
    },
    // 修改每页的显示条数
    handleSizeChange (val) {
      // 将用户键入的条数赋值给data的数据
      console.log(val)
      this.params.pagesize = val
      // 重新调用获取数据的函数
      this.getGoods()
      // console.log(`每页 ${val} 条`)
    },
    // 修改当前显示页
    handleCurrentChange (val) {
      this.params.pagenum = val
      this.getGoods()
      // console.log(`当前页: ${val}`)
    },
    addGoods () {
      this.$router.push('/goods/add')
    }
  }
}
</script>
<style lang="less" scoped>
// 卡片栏
.box-card {
  margin-top: 10px;
  // 搜索框
  .search {
    display: flex;
    .el-input-group {
      width: 40%;
      margin-right: 20px;
    }
  }
  .el-table {
    margin: 20px 0;
  }
}
</style>
