<template>
  <div class="addGoods_container">
    <!-- 面包屑导航 -->
    <bread :first="'商品管理'" :second="'添加商品'"></bread>
    <el-card class="box-card">
      <!-- 头部提示框 -->
      <el-alert title="添加商品信息" type="info" center show-icon> </el-alert>
      <!-- 步骤条 -->
      <el-steps :space="200" :active="active - 0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- Tab 标签页 -->
      <template>
        <el-tabs tab-position="left" style="height: 100%;" v-model="active" :before-leave="leaveTab">
          <!-- v-model绑定的是tab的name值 -->
          <el-tab-pane label="基本信息" name="0">
            <!-- 商品基本信息 -->
            <el-form :model="goodsForm" :rules="rules" ref="goodsForm" class="demo-ruleForm">
              <el-form-item label="商品名称" prop="goods_name">
                <el-input v-model="goodsForm.goods_name"></el-input>
              </el-form-item>
              <el-form-item label="商品价格" prop="goods_price">
                <el-input v-model="goodsForm.goods_price"></el-input>
              </el-form-item>
              <el-form-item label="商品重量" prop="goods_weight">
                <el-input v-model="goodsForm.goods_weight"></el-input>
              </el-form-item>
              <el-form-item label="商品数量" prop="goods_number">
                <el-input v-model="goodsForm.goods_number"></el-input>
              </el-form-item>
              <el-form-item label="商品分类">
                <!-- 级联选择框 -->

                <el-cascader :options="options" :props="setOptions" clearable filterable v-model="value" @change="change"></el-cascader>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <ul>
              <li v-for="(item, index) in manyTable" :key="index">
                <el-checkbox v-for="(item2, index2) in item.attr_vals" :key="index2" border checked style="margin:5px">{{ item2 }}</el-checkbox>
              </li>
            </ul>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <!-- 商品属性模板 -->
            <el-form :model="goodsForm" ref="ruleForm" class="demo-ruleForm">
              <el-form-item :label="item.attr_name" v-for="(item, index) in onlyTable" :key="index">
                <el-input v-model="item.attr_vals"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- 图片上传 -->
            <el-upload class="upload-demo" :action="url" :on-preview="handlePreview" :on-remove="handleRemove" :headers="resHeader" :file-list="fileList" :on-success="updatesucc" list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">
                只能上传jpg/png文件，且不超过500kb
              </div>
            </el-upload>
          </el-tab-pane>
          <!-- 富文本编辑器的使用 -->
          <el-tab-pane label="商品内容" name="4">
            <template>
              <div>
                <quill-editor v-model="goodsForm.goods_introduce" :options="editorOption" style="height:300px;" @change="onEditorChange()"></quill-editor>
              </div>
            </template>
            <!-- 提交按钮 -->
            <el-button type="primary" round style="margin-top: 100px;" @click="addGoodsInfo">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </template>
    </el-card>
  </div>
</template>
<script>
// 导入富文本编辑器包
import { quillEditor } from 'vue-quill-editor'
export default {
  name: '',
  data () {
    return {
      // 步骤栏
      active: '0',
      //   商品基本信息表单
      goodsForm: {
        goods_name: '', // 商品名称
        goods_price: '', // 价格
        goods_number: '', // 数量
        goods_weight: '', // 重量
        goods_cat: '', // 以为','分割的分类列表
        pics: [], // 存放上传的文件
        attrs: [], // 商品的参数（数组）
        goods_introduce: ''
      },
      // 三级商品分类的ID
      goodsId: '',

      // 级联选择框
      options: [],
      value: [], // 选中分类的值
      setOptions: {
        label: 'cat_name',
        value: 'cat_id'
        // checkStrictly: true
      },
      //   定义规则
      rules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' },
          { pattern: /^\d+$/, message: '价格只能是数字', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' },
          { pattern: /^\d+$/, message: '数量只能是数字', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品数量', trigger: 'blur' },
          { pattern: /^\d+$/, message: '数量只能是数字', trigger: 'blur' }
        ]
      },
      // 图片上传
      url: 'http://192.168.68.23:8080/api/private/v1/upload', // 上传的路径
      resHeader: {
        Authorization: localStorage.getItem('token')
      },
      // 上传的文件列表
      fileList: [],
      // 商品参数列表的参数
      manyTable: [],
      // 商品属性
      onlyTable: [],
      // 富文本编辑器的使用参数
      editorOption: {
        placeholder: '编辑文章内容'
      }
    }
  },
  created () {
    this.getLian()
  },
  methods: {
    //   获取商品参数数据
    async getLian () {
      // 获取联级下拉框数据
      const res = await this.axios.get('/categories/')
      this.options = res.data.data
      // console.log(res, 888888)
    },
    // 清空级联框
    change () {
      if (this.cascaderVal) {
        //   弹出警示框
        this.$message({
          message: '只能选择三级商品分类！',
          type: 'warning'
        })
        this.value = [] // 清空级联表单数据
      } else {
        // 已经选到了第三级，获取第三级的id一会添加使用
        this.goodsId = this.value[this.value.length - 1]
      }
    },
    // tab栏跳转拦截
    async leaveTab (activeName) {
      if (activeName !== 0) {
        if (this.cascaderVal) {
          return new Promise()
        }
      }

      // 判断去的是商品参数页
      if (activeName === '1') {
        // 发送请求渲染数据
        const res = await this.axios.get(`/categories/${this.value[this.value.length - 1]}/attributes`, { params: { sel: 'many' } })

        // 对拿到的字符串形式的数据处理，转化为数组存储
        res.data.data.forEach(ele => {
          ele.attr_vals = ele.attr_vals.split(',')
        })
        console.log(res, 9999999)
        this.manyTable = res.data.data
      } else if (activeName === '2') {
        // 去往的是商品属性
        const res = await this.axios.get(`/categories/${this.value[this.value.length - 1]}/attributes`, { params: { sel: 'only' } })
        // 将获取的数据存放到指定数组
        this.onlyTable = res.data.data
        console.log(res, this.onlyTable, 777777)
      }
    },
    // 图片上传
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    // 图片上传成功的钩子
    updatesucc (response, file, fileList) {
      this.goodsForm.pics.push(response.data.tmp_path)
      // console.log(response, 99999)
      // console.log(this.fileList);
    },
    // 富文本编辑器的处理函数
    onEditorChange ({ quill, html, text }) {
      console.log('editor change!', quill, html, text)
    },
    // 添加商品的函数
    async addGoodsInfo () {
      this.goodsForm.goods_cat = this.value.join(',')
      // 设定attrs参数
      const arr1 = this.manyTable.map(item => {
        return {
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(',')
        }
      })
      const arr2 = this.onlyTable.map(item => {
        return {
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        }
      })
      this.goodsForm.attrs = [...arr1, ...arr2]
      const res = await this.axios.post('/goods', this.goodsForm)
      if (res.data.meta.status === 201) {
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
      }
      this.$router.push('/goods')
    }
  },
  //   判断当前级联选择器勾选了几层
  computed: {
    cascaderVal: function () {
      if (this.value.length === 3) {
        return false
      } else {
        return true
      }
    }
  },
  components: {
    quillEditor // 注册富文本包的插件
  }
}
</script>
<style lang="less" scoped>
.addGoods_container {
  width: 100%;
  .el-card {
    margin-top: 20px;
    width: 100%;
    /deep/.el-card__body {
      width: 100%;
      //   height: 100%;
    }
    //   步骤条
    .el-steps {
      margin: 20px 0;
    }
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>
