<template>
  <div class="params_container">
    <!-- 面包屑导航 -->
    <bread :first="'商品管理'" :second="'参数列表'"></bread>
    <!-- 卡片 -->
    <el-card class="box-card">
      <!-- 警告框 -->
      <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" show-icon effect="dark"> </el-alert>
      <!-- 级联选择器 -->
      <el-cascader :options="options" :props="setOptions" clearable filterable v-model="value" @change="choose"></el-cascader>

      <!-- Tab栏切换 -->
      <el-tabs type="border-card" v-model="activeName">
        <el-tab-pane label="动态参数" name="many">
          <el-button type="primary" size="mini" :disabled="flag" @click="dialogFormVisible = true">添加参数</el-button>
          <!-- 表格 -->
          <template>
            <el-table :data="manyData" style="width: 100%" border>
              <el-table-column width="50" type="expand" :rules="rules">
                <template slot-scope="scope">
                  <!-- <pre>{{ scope.row }}</pre> -->

                  <el-tag :disable-transitions="false" v-for="(tag, index) in scope.row.attr_vals" :key="index" closable @close="handleClose(scope.row, index)">
                    {{ tag }}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    style="width:100px"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                </template>
              </el-table-column>
              <el-table-column label="#" width="50" type="index"> </el-table-column>
              <el-table-column label="参数名称">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.attr_name }}</span>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="180">
                <template slot-scope="scope">
                  <el-button size="mini" type="primary" icon="el-icon-edit" @click="openEdit(scope.row)">编辑</el-button>
                  <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteParams(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button type="primary" size="mini" :disabled="flag" @click="dialogFormVisibleStatic = true">添加属性</el-button>
          <!-- 表格 -->
          <template>
            <el-table :data="onlyData" style="width: 100%" border>
              <el-table-column width="50" type="expand">
                <template slot-scope="scope">
                  <el-tag :disable-transitions="false" v-for="(tag, index) in scope.row.attr_vals" :key="index" closable @close="handleClose(scope.row, index)">
                    {{ tag }}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    style="width:100px"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                </template>
              </el-table-column>
              <el-table-column label="#" width="50" type="index"> </el-table-column>
              <el-table-column label="参数名称">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.attr_name }}</span>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="180">
                <template>
                  <el-button size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
                  <el-button size="mini" type="danger" icon="el-icon-delete">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 添加动态参数的对话框 -->
    <el-dialog title="添加动态参数" :visible.sync="dialogFormVisible">
      <el-form :model="form" :rules="rules" ref="addForm">
        <el-form-item label="动态参数" :label-width="formLabelWidth" prop="attr_name">
          <el-input v-model="form.attr_name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addBtn('addForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 添加静态属性-->
    <el-dialog title="添加静态属性" :visible.sync="dialogFormVisibleStatic">
      <el-form :model="static_form" :rules="rules" ref="static_form">
        <el-form-item label="静态属性" :label-width="formLabelWidth" prop="static_name">
          <el-input v-model="static_form.static_name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleStatic = false">取 消</el-button>
        <el-button type="primary" @click="addBtn('static_form')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改动态参数框 -->
    <el-dialog title="修改动态参数" :visible.sync="dialogFormVisibleDynamic">
      <el-form :model="dynamicForm" :rules="rules" ref="dynamicForm">
        <el-form-item label="动态参数" :label-width="formLabelWidth" prop="attr_name">
          <el-input v-model="dynamicForm.attr_name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleDynamic = false">取 消</el-button>
        <el-button type="primary" @click="confirmEdit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: '',
  data () {
    return {
      // 级联选择框
      options: [],
      value: [], // 选中分类的值
      setOptions: {
        label: 'cat_name',
        value: 'cat_id'
      }, // 级联选择器的数据
      // Tab栏切换
      activeName: 'many',
      manyData: [],
      onlyData: [],
      // inputVisible: false, //控制是否变化为input框
      // inputValue: '' //input 框的内容

      // 添加动态参数对话框的参数
      dialogFormVisible: false,
      form: {
        attr_name: ''
      },
      formLabelWidth: '120px',
      rules: {
        attr_name: [
          { required: true, message: '请输入动态参数名称', trigger: 'blur' },
          { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
        ],
        static_name: [
          { required: true, message: '请输入静态属性名称', trigger: 'blur' },
          { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
        ]
      },
      // 添加静态参数
      dialogFormVisibleStatic: false,
      static_form: {
        static_name: ''
      },
      // 修改动态参数
      dialogFormVisibleDynamic: false,
      dynamicForm: {
        attr_name: '',
        id: '',
        attr_sel: 'many' // 属性的类型
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    // 获取数据
    async getData () {
      const res = await this.axios.get('/categories/')
      this.options = res.data.data
    },
    // 级联选择器选中事件
    async choose () {
      // 满足级联条件
      if (!this.flag) {
        // 发送请求 获取表单数据
        // only的数据
        const res = await this.axios.get(`/categories/${this.CateId}/attributes`, { params: { sel: 'only' } })
        this.disposeArr(res.data.data)
        this.onlyData = res.data.data

        // many的数据
        const res1 = await this.axios.get(`categories/${this.CateId}/attributes`, { params: { sel: 'many' } })
        this.disposeArr(res1.data.data)
        this.manyData = res1.data.data
      }
    },
    // 处理数据
    disposeArr (arr) {
      // 处理字符串的参数值
      arr.forEach(ele => {
        ele.attr_vals = ele.attr_vals.split(',').filter(item => {
          return item !== ''
        })
        ele.inputVisible = false
        ele.inputValue = ''
      })
      return arr
    },
    // 点击新增标签事件
    showInput (rows) {
      rows.inputValue = ''
      rows.inputVisible = true
      // 聚焦 $nextTick 在数据更新之后执行的回调
      this.$nextTick(() => {
        this.$refs.saveTagInput.focus()
      })
    },
    // 失去焦点 添加数据
    async handleInputConfirm (rows) {
      var options = {
        attr_name: rows.attr_name,
        attr_sel: this.activeName,
        attr_vals: [...rows.attr_vals, rows.inputValue.trim()].join(',')
      }
      if (rows.inputValue.trim() !== '') {
        const res = await this.axios.put(`/categories/${this.CateId}/attributes/${rows.attr_id}`, options)
        if (res.data.meta.status === 200) {
          // 添加成功
          this.$notify({
            title: '成功',
            message: res.data.meta.msg,
            type: 'success'
          })
          // 实现页面更新
          rows.attr_vals.push(rows.inputValue)
        }
      }
      rows.inputVisible = false // 关闭输入框
    },
    // 删除标签
    handleClose (val, index) {
      this.$confirm('此操作将永久删除参数, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          // 删除数组中的值
          val.attr_vals.splice(index, 1)
          var obj = {
            attr_name: val.attr_name,
            attr_sel: this.activeName,
            attr_vals: val.attr_vals.join(',')
          }
          const res = await this.axios.put(`categories/${this.CateId}/attributes/${val.attr_id}`, obj)
          console.log(res, 88888)
          if (res.data.meta.status === 200) {
            this.$notify({
              title: '成功',
              message: '删除成功!',
              type: 'success'
            })
          }
        })
        .catch()
    },
    // 添加参数
    addBtn (addForm) {
      // 设置请求类型
      this.form.attr_sel = this.activeName
      // 做二次校验
      this.$refs[addForm].validate(async valid => {
        if (valid) {
          // 发送请求
          const res = await this.axios.post(`/categories/${this.CateId}/attributes`, this.form)
          if (res.data.meta.status === 201) {
            this.choose()
            this.$message({
              showClose: true,
              message: res.data.meta.msg,
              type: 'success'
            })
            this.dialogFormVisible = false
          }
          this.$refs[addForm].resetFields()
        }
      })
    },
    // 打开修改框
    openEdit (rows) {
      console.log(rows, 8888)
      this.dynamicForm.attr_name = rows.attr_name
      this.dynamicForm.id = rows.attr_id
      this.dialogFormVisibleDynamic = true
    },
    // 确认修改参数
    async confirmEdit () {
      const res = await this.axios.put(`/categories/${this.CateId}/attributes/${this.dynamicForm.id}`, this.dynamicForm)
      if (res.data.meta.status === 200) {
        // 修改成功
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
        this.choose()
      } else {
        this.$message.error('修改失败!')
      }
      this.dialogFormVisibleDynamic = false
    },
    // 删除参数
    deleteParams (rows) {
      this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.axios.delete(`/categories/${this.CateId}/attributes/${rows.attr_id}`)
          if (res.data.meta.status === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.choose()
          }
        })
        .catch(() => {})
    }
  },
  computed: {
    flag () {
      // 判断是否选中级联选择器
      if (this.value.length === 3) {
        return false
      } else {
        return true
      }
    },
    // 计算得到分类的id
    CateId () {
      return this.value[this.value.length - 1]
    }
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 20px;
  // 警告框
  .el-alert {
    margin-bottom: 20px;
  }
  // Tab栏切换
  .el-tabs {
    margin-top: 20px;
    .el-tag {
      margin: 5px;
    }
  }
}
</style>
